import React, { Suspense, lazy }from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Col,
  Card,
  CardText,
  CardBody,
  CardHeader,
} from 'reactstrap';
import { Helmet } from 'react-helmet';
import { Link } from 'react-router-dom';
import { errorMessages } from '../../../constants/messages';
import Loading from '../UI/Loading';
import Error from '../UI/Error';

const GameView = ({
  error,
  loading,
  games,
  gameId,
}) => {
  // Loading
  if (loading) return <Loading />;

  // Error
  if (error) return <Error content={error} />;

  // Get this game from all games
  let game = null;
  if (gameId && games) {
    game = games.find(item => item.id === gameId);
  }
  // game not found
  if (!game) return <Error content={errorMessages.game404} />;

  const GameComponent = React.lazy(() => import('./AllGames/'+gameId+'/'+gameId));

  return (
    <div>
      <Helmet>
        <title>{game.title}</title>
      </Helmet>

      <Row className="pb-3">
        <Col sm="12">
          <Link className="btn btn-primary" to="/games">
            <i className="icon-arrow-left" />
            {' Back'}
          </Link>
        </Col>
      </Row>
      <Row>
        <Col sm="12">
          <h1>
            {game.title.length >= 100 ? `${game.title.substring(0, 100)} ...` : game.title}
          </h1>
          <p>
            {'by: '}
            {game.author}
          </p>
        </Col>
      </Row>
      <Suspense fallback={<div>Loading...</div>}>
        <GameComponent />
      </Suspense>
    </div>
  );
};

GameView.propTypes = {
  error: PropTypes.string,
  loading: PropTypes.bool.isRequired,
  gameId: PropTypes.string.isRequired,
  games: PropTypes.arrayOf(PropTypes.shape()).isRequired,
};

GameView.defaultProps = {
  error: null,
};

export default GameView;
