import React from 'react';
import PropTypes from 'prop-types';
import {
  Row,
  Card,
  CardText,
  CardBody,
  CardTitle,
  CardImg,
} from 'reactstrap';
import { Link } from 'react-router-dom';
import Error from '../UI/Error';
const image = (item) => {
  if (item.image !== '') {
    return (
      <CardImg style={{ maxHeight: '10vh', maxWidth: '10vw' }} top src={item.image} alt={item.title} />
    );
  }
};
const GameListing = ({ error, loading, games }) => {
  // Error
  if (error) return <Error content={error} />;
  // Build Cards for Listing
  const cards = games.map(item => (

    <Card className="p-card" key={`${item.id}`}>
      <CardBody>  
      <Link to={`/game/${item.id}`}>
        {image(item)}
        </Link>  
        <CardTitle>
          {item.title.length >= 60 ? `${item.title.substring(0, 60)} ...` : item.title}
        </CardTitle>
        <CardText>
          {item.body.length >= 60 ? `${item.body.substring(0, 60)} ...` : item.body}
        </CardText>
        <Link className="btn btn-primary" to={`/game/${item.id}`}>
          {'View Game '}
          <i className="icon-arrow-right" />
        </Link>
      </CardBody>
    </Card>
  ));

  // Show Listing
  return (
    <div>
      <Row className={loading ? 'content-loading' : ''}>
        {cards}
      </Row>
    </div>
  );
};

GameListing.propTypes = {
  error: PropTypes.string,
  loading: PropTypes.bool.isRequired,
  games: PropTypes.arrayOf(PropTypes.shape()).isRequired,
};

GameListing.defaultProps = {
  error: null,
};

export default GameListing;
