import { Firebase, FirebaseRef } from '../lib/firebase';

/**
  * Get gameTypes
  */
export function getGameTypes() {
  if (Firebase === null) return () => new Promise(resolve => resolve());

  return dispatch => new Promise((resolve, reject) => FirebaseRef.child('gameTypes').once('value')
    .then((snapshot) => {
      const data = snapshot.val() || [];
      return resolve(dispatch({ type: 'GAMETYPES_REPLACE', data }));
    }).catch(reject)).catch((err) => { throw err.message; });
}

/**
  * Get games
  */
export function getGames() {
  if (Firebase === null) return () => new Promise(resolve => resolve());

  return dispatch => new Promise(resolve => FirebaseRef.child('games')
    .on('value', (snapshot) => {
      const data = snapshot.val() || [];
      return resolve(dispatch({ type: 'GAMES_REPLACE', data }));
    })).catch((err) => { throw err.message; });
}
