import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import { getGames, getGameTypes } from '../actions/games';

class GameListing extends Component {
  static propTypes = {
    Layout: PropTypes.func.isRequired,
    games: PropTypes.shape().isRequired,
    match: PropTypes.shape({ params: PropTypes.shape({}) }),
    fetchGames: PropTypes.func.isRequired,
    fetchGameTypes: PropTypes.func.isRequired,
  }

  static defaultProps = {
    match: null,
  }

  state = {
    error: null,
    loading: false,
  }

  componentDidMount = () => this.fetchData();

  /**
    * Fetch Data from API, saving to Redux
    */
   fetchData = (data) => {
     const { fetchGames, fetchGameTypes } = this.props;
     this.setState({ loading: true });
     return fetchGames(data)
       .then(() => fetchGameTypes())
       .then(() => this.setState({
         loading: false,
         error: null,
       })).catch(err => this.setState({
         loading: false,
         error: err,
       }));
   }

  render = () => {
    const { Layout, games, match } = this.props;
    const { loading, error } = this.state;
    const id = (match && match.params && match.params.id) ? match.params.id : null;
    return (
      <Layout
        gameId={id}
        error={error}
        loading={loading}
        games={games.games}
        reFetch={() => this.fetchGames()}
      />
    );
  }
}

const mapStateToProps = state => ({
  games: state.games || {},
});

const mapDispatchToProps = {
  fetchGames: getGames,
  fetchGameTypes: getGameTypes,
};

export default connect(mapStateToProps, mapDispatchToProps)(GameListing);
